// @import "compass/css3"

$body-bg: #c1bdba;
$form-bg: #13232f;
$white: #ffffff;

$main-light: lighten($light-blue,5%);
$main-dark: darken($light-blue,5%);

$gray-light: #a0b3b0;
$gray: #ddd;

$thin: 300;
$normal: 400;
$bold: 600;
$br: 4px;

#login-components {
  a {
    text-decoration:none {}
    color: $light-blue;
    transition: .5s ease;
    &:hover {
      color: $main-dark; } }


  .form {
    background: rgba($form-bg,.9);
    padding: 40px;
    max-width: 600px;
    margin: 40px auto;
    border-radius: $br;
    box-shadow: 0 4px 10px 4px rgba($form-bg,.3); }


  .tab-group {
    list-style: none;
    padding: 0;
    margin: 0 0 40px 0;
    &:after {
      content: "";
      display: table;
      clear: both; }

    li a {
      display: block;
      text-decoration: none;
      padding: 15px;
      background: rgba($gray-light,.25);
      color: $gray-light;
      font-size: 20px;
      float: left;
      width: 50%;
      text-align: center;
      cursor: pointer;
      transition: .5s ease;
      &:hover {
        background: $main-dark;
        color: $white; } }

    .active a {
      background: $light-blue;
      color: $white; } }


  .tab-content > div:last-child {
 }    // display: none



  h1 {
    text-align: center;
    color: $white;
    font-weight: $thin;
    margin: 0 0 40px; }


  label {
    position: absolute;
    transform: translateY(6px);
    left: 13px;
    color: rgba($white,.5);
    transition: all 0.25s ease;
    -webkit-backface-visibility: hidden;
    pointer-events: none;
    font-size: 22px;
    .req {
    	margin: 2px;
    	color: $light-blue; } }


  label.active {
    transform: translateY(-20px);
    left: 2px;
    font-size: 14px;
    .req {
      opacity: 0; } }


  label.highlight {
  	color: $white; }


  input, textarea {
    font-size: 22px;
    display: block;
    width: 100%;
    height: 100%;
    padding: 5px 10px;
    background: none;
    background-image: none;
    border: 1px solid $gray-light;
    color: $white;
    border-radius: 0;
    transition: border-color .25s ease, box-shadow .25s ease;
    &:focus {}
  		outline: 0;
  		border-color: $light-blue; }


  textarea {
    border: 2px solid $gray-light;
    resize: vertical; }


  .field-wrap {
    position: relative;
    margin-bottom: 40px; }


  .top-row {
    &:after {
      content: "";
      display: table;
      clear: both; }

    > div {
      float: left;
      width: 48%;
      margin-right: 4%;
      &:last-child {
        margin: 0; } } }


  .button {
    border: 0;
    outline: none;
    border-radius: 0;
    padding: 15px 0;
    font-size: 2rem;
    font-weight: $bold;
    text-transform: uppercase;
    letter-spacing: .1em;
    background: $light-blue;
    color: $white;
    transition: all.5s ease;
    -webkit-appearance: none;
    &:hover, &:focus {
      background: $main-dark; } }


  .button-block {
    display: block;
    width: 100%; }


  .forgot {
    margin-top: -20px;
    text-align: right; }

  .login-error {
    color: #fff; } }



